import { EmailInput, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components'
import Registration from '../../components/registration/registration';
import AppHeader from '../../components/app-header/app-header';
import { REGISTER_ROUTE, FORGOT_PASSWORD_ROUTE } from '../../utils/routes';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { setUserFormValue } from '../../services/actions/login-form';
import { authUser } from '../../utils/api';

export function LoginPage() {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { loginRequest, form } = useSelector((store) => store.loginForm);

    const onFormChange = (evt) => {
        dispatch(setUserFormValue(evt.target.name, evt.target.value))
    }

    const onFormSubmit = (evt) => {
        evt.preventDefault();
        dispatch(authUser(form, () => navigate('/', { replace: true })))
    }

    return (
        <>
        <AppHeader/>
        <Registration
            title=''
            btn_name=''
            additional_texts={[{'text': '   ?', 'link': `/${REGISTER_ROUTE}`, 'link_text': ''},
                               {'text': ' ?', 'link': `/${FORGOT_PASSWORD_ROUTE}`, 'link_text': ' '}]}
            onFormSubmit={onFormSubmit}
            disabled={loginRequest}
        >
            <EmailInput 
                placeholder={'E-mail'}
                onChange={onFormChange} 
                value={form.email}
                size={'default'}
                extraClass="mt-6"
                name='email'
            />
            <PasswordInput 
                placeholder={''} 
                onChange={onFormChange} 
                value={form.password}
                size={'default'}
                icon={'ShowIcon'}
                extraClass="mt-6"
                className='text input__textfield text_type_main-default pr-6'
                name='password'
            />
        </Registration>
        </>
    );
  }